import React, { useEffect } from 'react';
import classes from './Posts.module.css';
import ava from '../../assets/avaImg/mal2.jpg';
import tochki from '../../assets/avaImg/tochki.png';


const Posts = (props) => {

    const [popap, setPopap] = React.useState(false);
    const referens = React.useRef();
    const treftochki = React.useRef();

    const hidPopap = (e) => {
        if (!e.path.includes(referens.current) && !e.path.includes(treftochki.current)) {
            setPopap(false)
        }
    }

    React.useEffect(() => {
        document.body.addEventListener('click', hidPopap)
        return () => {
            document.body.removeEventListener('click', hidPopap)
        }
    }, [])

    if (!props.profile) {
        return null
    }



    return (
        <div className={classes.Posts}>
            <div className={classes.imgZone}>
                <div className={classes.PostImg}>
                    <img src={props.profile.photos.large ? props.profile.photos.large : ava} />
                </div>
                {/* <div className={classes.likesZone}>
                    <div className={classes.likesWord}>
                        нравится
                    </div>
                    <div className={classes.likes}>
                        {props.likes}
                    </div>
                </div> */}
                {/* {props.isOuner ? <button onClick={() => props.deletePostAC(props.id)}>удалить пост</button> :
                    <button>лайк</button>} */}
            </div>
            <div className={classes.menu}>
                <div className={classes.postMessage}>
                    <div className={classes.text}>{props.message}</div>
                    <div className={classes.likesZone}>
                        {/* <span className={classes.likes}> лайк {props.likes}</span> */}
                    </div>
                </div>
                <img src={tochki} ref={treftochki} alt="menu" onClick={() => setPopap(popap => !popap)} />
                {popap ? <div className={classes.delete} ref={referens}>
                    {props.isOuner ?
                        <div>
                            <div onClick={() => props.deletePostAC(props.id)} className={classes.item}>удалить пост</div>
                            <div style={{ opacity: 0.3 }} className={classes.item}>переслать</div>
                            <div style={{ opacity: 0.3 }} className={classes.item}>изменить</div>
                        </div>
                        :
                        ''
                    }
                </div> : ''}
            </div>
        </div>

    )
};

export default Posts;
